/*
    Import variables and tools
 */
@import '../var';
@import '../tools/mixins';


.col-lg-6.pullTop10 {
    margin-top: -13px;
}
span.hideHeader {
    color: #237ac0;
}
.hideNonMember {
    display: none !important;
}
img.greenCheck {
    margin-bottom: -5px;
}
.location-wrap {
    padding-top: 12px !important;
}
.row.topOwnerPanel {
    padding-top: 0px;
    margin-left: 0px;   
}
.page-wrapper .container.propertyInfo {
    background: #fff;
    margin: -30px auto 20px;
    padding: 5px 20px 0 20px !important;
    box-shadow: 0 2px 24px rgba(0,0,0,0.1) !important;
    overflow: hidden;
}
.propertyInfoTable th {
    text-align: left;
    padding-left: 20px;
    font-size: 20px;
}
a.propertySaveBtn img {
    margin-top: 0px !important;
    padding-top: 0px !important;
    position: relative;
    top: 6px;
}
.propertyInfoTable th {
    background: #237ac0 !important;
}
.page-wrapper .form-wrap {
    background: #237ac0 !important;
}
.editColor {
    color: #237ac0 !important;
}
span.propertyInfoOwnerTitle {
    position: relative;
    top: -7px;
}
span.propertyDetailsTable {
    position: relative;
    top: 1px;
}
tfoot.shareFooter {
    background: #237ac0;
}
.propertyInfoTable td {
    font-weight: 600;
    padding: 10px 20px;
    border-top: 1px solid rgba(55,120,173,0.2) !important;
    border-left: 1px solid rgba(55,120,173,0.2) !important; 
    border-bottom: 0 !important;
    border-right: 0 !important;
}
td.hd.sharePropertyArea {
    padding: 16px 20px 6px 20px;
}
strong.hidden-lg.sharePropertyTitle {
    font-size: 17px;
    font-weight: bold;
    position: relative;
    top: 5px;
}
select.field.input.rounded {
    border-radius: 5px !important;
    padding-left: 10px;
}
.margin-top {
    margin-top: 20px !important;
}
a.termsOfUse {
    color: #fff!important;
    font-weight: 400 !important;
}
td.col {
    background: #8ec1f6 !important;
    font-weight: 700 !important;
    padding: 12px 5px 12px 10px;
}
a.propertyTooltip {
    position: relative;
    top: 5px;
    left: 2px;
}
.propertyTooltip img {
    width: 20px !important;
    height: 20px !important;
}
div#propertyDetails.page-section {
    padding: 0px 20px 40px 20px;
}
#propertyDetails .row {
    padding-top: 20px;
    margin-left: 0px;
}
.propertAddress {
    font-size: 16px;
    text-transform: uppercase;
    padding-top: 10px;
    color: #8ecbfc; 
}
.lowerDetails .col {
    width: 230px !important;
}
.transHistoryAccord {
    border: 2px solid #237ac0;
    border-radius: 3px;
    padding: 12px 20px;
}
.transHistoryAccord .accordion-title {
    text-align: left;
    color: #237ac0;
    font-size: 20px;
    font-weight: 600;
    padding-left: 20px;
}
.transHistoryAccord .accordion-title:before {
    position: absolute !important;
    left: 20px !important;
    top: 13px !important;
    font-size: 20px !important;
    opacity: 1 !important;
}
.transHistoryAccord .accordion-content {
    margin-bottom: 0px !important;
}
table.propertyInfoTable.transactionDetails {
    margin-bottom: 10px !important;
}
div#mapStreetView.page-section {
    padding: 20px 20px 40px 20px;
    margin-top: -40px;
    background: #f5f7f9;    
}
a.editColour {
    color: #237ac0 !important;  
}
.transactionDetails .col {
    width: 250px !important;
}
.propertyInfoForm input {
    border-color: #237ac0 !important;
}
.propertyInfoForm select {
    border-color: #237ac0 !important;
    margin-top: -10px;
}
.firstNameWidth {
    width: 179px !important;
}